


















import Vue from 'vue';
import { Moment } from 'moment';

export default Vue.component('Post', {
  props: {
    summary: Object,
    content: String,
  },
  computed: {
    formattedDate(): string {
      return this.summary.date.format('MMM DD, YYYY');
    },
    tags(): string[] {
      return this.summary.tags;
    }
  },
});
